@import url(https://fonts.googleapis.com/css?family=Lobster|Quicksand|Amatic+SC&display=swap);
.full {
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  font-size: 250%;
  font-family: 'Amatic SC', cursive;
  text-align: center;
}
.full a:not(.welcome):not(.home) {
  line-height: 30vh;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid; */
  color: gold;
  text-shadow: 2px 2px 2px grey;
  font-size:50px;
  padding:10px;
  /* border-radius:50%; */
}
.full a:not(.welcome):hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.full i:hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.responsive {
  display: none;
}
.home {
  grid-area: 1 / 1 / 4 / 1;
  line-height: 60vh;
}
.rose {
  grid-area: 1 / 4 / 5 / 5;
}
.rose img {
  width: 80vh;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  text-align: right;
  /* border-top: 4px black solid; */
  background-image: url(/portfolioPresent/static/media/final824-roses-drawing.9c0c16ad.svg);
  background-size: cover;
  background-position: left;
  display: grid;
  place-items: center;
  border-left: none !important;
  text-shadow: 2px 2px black;
}
nav p {
  /* border-bottom: 4px black solid; */
  font-size: 1.5em;
  text-align: center;
  padding:5%;
}
.iconsnav {
  border: none;
  font-size: 1em;
  grid-area: 4 / 1 / 4 / 4;
  display: inline;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.iconsnav i {
  color: gold;
}

.about {
  padding: 0 50px 50px 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4)
}
.about > p {
  font-family: "Quicksand", sans-serif;
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
  margin: 0 15px 0 15px;
  padding: 15px;
  text-align: justify;
}
.about > p:nth-of-type(-n+2){
  margin-bottom: 30px; 
}
.date p {
  background-color: gold;
  width: 80px;
  height: 80px;
  line-height: 80px;
  border: 4px gold solid;
  border-radius: 50%;
  font-size: 3em;
}
.date {
  font-family: "Lobster", cursive;
  padding: 15px 30px 15px 20px;
  float: right;
  shape-outside: circle(50%);
}

.projects {
  padding: 70px 50px 50px 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

h2 {
  font-size: 166%;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
}
h3 {
  font-family: "Quicksand", sans-serif;
}
.imagecenter {
  padding: 15px;
  text-align: center;
}
.projects img {
  padding-top:70px;
  max-width: 100%;
  max-height: 100%;
}
.projects ul {
  margin-left: 15px;
  padding: 15px 15px 0;
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
}
.projects ul:nth-of-type(2) {
  margin-bottom: 0;
}
.projects li {
  padding-bottom: 5px;
  font-family: "Quicksand", sans-serif;
}
.projects li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
  border-bottom: 1px solid gold;
}
.projects i {
  padding: 15px;
}
.projects li a:hover {
  padding-bottom: 18px;
  border-bottom: 1px solid gold;
}

.contact {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  font-family: "Quicksand", sans-serif;
  padding: 50px;
  margin-top:10%;
}
form {
  margin-left: 15px;
}
input,
textarea {
  width: 40vw;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1.3em;
  font-family: "Quicksand", sans-serif;
}
textarea {
  height: 20vw;
}
.contact ul li:nth-child(1n + 1) {
  border-bottom: 1px solid gold;
}
.avatar {
  align-self: center;
  margin: 0 auto;
  background-image: url(/portfolioPresent/static/media/myAvatar.97a8dc08.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 21vw;
  width: 21vw;
  border-radius: 50%;
  border: 3px solid white;
  text-align: center;
  margin-bottom: 25px;
}
h4 {
  display: inline-block;
  color: gold;
  font-size: 2em;
  position: relative;
  top: 101%;
  text-shadow: 2px 2px 2px black;
  font-family: 'Amatic SC', cursive;
  line-height: 50px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-moz-placeholder,
textarea::-moz-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::placeholder,
textarea::placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input[value="send"]:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* border page */

#top, #bottom, #left, #right {
	background: gold;
  position: fixed;
  z-index:1;
	}
	#left, #right {
		top: 0; bottom: 0;
		width: 5px;
		}
		#left { left: 0; }
		#right { right: 0; }
		
	#top, #bottom {
		left: 0; right: 0;
		height: 5px;
		}
		#top { top: 0; }
    #bottom { bottom: 0; }
    
    .App{
      height:100vh;
    }
  body {
      margin: 0;
      padding: 20px;
      min-height: 100vh;
      width: 100%;
      background: url(/portfolioPresent/static/media/background.d891b24c.jpg) no-repeat center center fixed;
    }

.projects,
.about,
.contact {
  margin: 15px auto;
  background-color: rgba(0, 0, 0, 0.4);
}
.center{
  text-align:center;
}
.divider{
  width: 111px;
  display: inline-block;
  border: none;
  border-bottom: 5px solid gold;
}
h1 {
  font-size: 60px;
  color: white;
  /* margin: 70px 0 70px 0; */
  text-align:center;
  font-family: 'Amatic SC', cursive;
}
h3,
p,
a,
i,
span {
  color: white;
}
.up {
  position: fixed;
  right: 3%;
  bottom: 2vh;
  z-index: 1;
}
.goback {
  font-size: 2em;
}
.goback:hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.navstyle {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  text-align: center;
}
.navstyle i {
  color: grey;
  font-size: 2em;
  padding:5px;
}
.navstyle a,li {
  padding: 0 3% 0 3%;
}
.navstyle i:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}
/* footer end */

/* animation */

.puff-in-center{-webkit-animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both;animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both}

/* keyframes */

@-webkit-keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

@keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

.App-header {
  font-size: calc(5px + 2vmin);
}
@media screen and (max-width: 768px) {
  body {
    -ms-scroll-chaining: chained;
        overscroll-behavior: auto;
  }
  .full {
    display: none;
  }
  .responsive{
    display:flex;
    justify-content: space-around;
  } 
  .resWelcome{
    font-size: 5em;
    padding: 5%;
    text-align: end;
    text-shadow: 1px 1px black;
    margin-top: 10%;
    font-family: 'Amatic SC', cursive;
  }
  main {
    border: none;
  }
  .about,
  .projects {
    padding: 0 0 30px 0;
  }
  h1 {
    font-size: 3em;
  }
  .projects li:nth-child(6) {
    font-size: 2em;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 30px;
  }
  .projects li a {
    padding: 15px;
    border: 2px solid white;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    display: grid;
    place-content: center;
  }
  ul span {
    display: none;
  }
  .contact form {
    display: none;
  }
  .avatar {
    width: 61vw;
    height: 61vw;
  }
  .resFooter{

display:flex;
justify-content: space-around;
font-size: 3em;
  }
  .bottom{
    position:absolute;
    bottom: 5px;
    left:0;
    right:0;
    text-align:center;
  }
  .bottom a{
    margin:30px
  }
  .bottom i{
    color: grey;
  }
  footer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
}
